import React, { useEffect, useState } from 'react';
import { ReactEditor } from '@block-editor/editor-base';
import { registerMicroApps, start } from 'qiankun';
import './App.css';

// 
const microApps = [
  {
    name: 'demo-micro-app',
    entry: '//localhost:3001',
    container: '#micro-app-container',
    activeRule: '/demo',
  },
  {
    name: 'pyramid-app',
    entry: '//localhost:3002',
    container: '#micro-app-container',
    activeRule: '/pyramid',
  },
  {
    name: 'chart-app',
    entry: '//localhost:3003',
    container: '#micro-app-container',
    activeRule: '/chart',
  }
];

const App: React.FC = () => {
  const [editorInstance, setEditorInstance] = useState<any>(null);
  const [microName, setMicroName] = useState('demo-micro-app');
  const [wsUrl, setWsUrl] = useState('ws://localhost:1234');
  const [availableMicroApps] = useState(microApps.map(app => app.name));

  useEffect(() => {
    // 
    registerMicroApps(microApps);
    
    // qiankun
    start({
      sandbox: {
        strictStyleIsolation: true,
        experimentalStyleIsolation: true,
      }
    });
  }, []);

  const handleEditorReady = (editor: any) => {
    console.log(' :', editor);
    setEditorInstance(editor);
    
    // SkeletonNode
    setTimeout(() => {
      editor.insertSkeletonNode();
    }, 1000);
  };

  const handleInsertSkeletonNode = () => {
    if (editorInstance) {
      editorInstance.insertSkeletonNode();
    }
  };

  const handleMicroNameChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setMicroName(e.target.value);
  };

  const handleWsUrlChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setWsUrl(e.target.value);
  };

  return (
    <div className="app">
      <header className="app-header">
        <h1>Block Editor </h1>
        <div className="controls">
          <div className="control-group">
            <label htmlFor="microName">:</label>
            <select
              id="microName"
              value={microName}
              onChange={handleMicroNameChange}
            >
              {availableMicroApps.map(appName => (
                <option key={appName} value={appName}>
                  {appName}
                </option>
              ))}
            </select>
          </div>
          <div className="control-group">
            <label htmlFor="wsUrl">WebSocket:</label>
            <input
              id="wsUrl"
              type="text"
              value={wsUrl}
              onChange={handleWsUrlChange}
              placeholder="WebSocket"
            />
          </div>
          <button onClick={handleInsertSkeletonNode} className="insert-btn">
            
          </button>
        </div>
      </header>
      
      <main className="app-main">
        <div className="editor-container">
          <ReactEditor
            microName={microName}
            wsUrl={wsUrl}
            onEditorReady={handleEditorReady}
          />
        </div>
      </main>
    </div>
  );
};

export default App;
